import React, { Component } from 'react'


import Masonry from 'react-masonry-component';

import Enduins from '../Images/Graphic Design/Enduins/enduins.jpg'
import AbraNewDay from '../Images/Graphic Design/Personal/Abra-New-Day.jpg'
import GoldenHour from '../Images/Graphic Design/Personal/goldern-hour-optmz.jpg'
import Thought from '../Images/Graphic Design/Personal/thoughts-optmz.jpg'

let slide = 
[
  {
    title: 'Enduins',
    image: Enduins,
  },
  {
    title: 'Abra New Day',
    image: AbraNewDay,
  },
  {
    title: 'Golden Hour',
    image: GoldenHour,
  },
  {
    title: 'Thought',
    image: Thought,
  },
]

class Home extends Component {
  constructor(props){
    super(props)
    this.state = { }
    
  }
 
  render() {
      const childElements = slide.map(function(attr){
           return (
              <div className='image-mason-container'>
                <div className='image-mason' >
                    <img src={attr.image} />
                </div>
              </div>
            );
        });
        return (
            <Masonry
                className={'my-gallery-class'} // default ''   
            >
                {childElements}
            </Masonry>
          
        );
  }
}

export default Home
