import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import Routes from './Routes'

class Work extends Component {
  constructor(props){
    super(props)
    this.state = {
        active: false,
        nav: 'Logo',
    }
    this.navToggle = this.navToggle.bind(this)
    this.changeLink = this.changeLink.bind(this)
  }
  navToggle(){
    this.state.active ? this.setState({ active: false}) : this.setState({ active: true})
  }
  changeLink(link){
    this.setState({
      nav: link,
      active: false
    })
  }
  render() {
        return (
          <div className='work-container'>
            <div className='work-title-container'>
             <div className='work-title'>Selected &nbsp;</div>
              <div className='work-nav'>
                  <div onClick={this.navToggle}>{this.state.nav} <i className={this.state.active ? 'fa fa-chevron-up' : 'fa fa-chevron-down'}></i></div>
                  {this.state.active ? 
                    <div className='nav-link'>
                      <div onClick={()=>this.changeLink('Logo')}>
                        <Link to='/works/logo'>Logo</Link>
                      </div>
                      <div onClick={()=>this.changeLink('Graphics')}>
                        <Link to='/works/graphics'>Graphics</Link>
                      </div>
                      <div onClick={()=>this.changeLink('UI/UX')}>
                        <Link to='/works/ui-ux'>UI/UX</Link>
                      </div>
                    </div>

                    : null}
                
              </div>

              &nbsp;
              <div className='work-title'>Designs</div>
              </div>
            <Routes />
          </div>
        );
  }
}

export default Work
