import React, { Component } from 'react'


import Masonry from 'react-masonry-component';

import EnduinsPrototype from '../Images/UXUI Design/Enduins-WebPrototype_Homepage.jpg'
let slide = 
[
 {
    title: 'Enduins Prototype',
    image: EnduinsPrototype,
  },
]

class Home extends Component {
  constructor(props){
    super(props)
    this.state = { }
    
  }
 
  render() {
      const childElements = slide.map(function(attr){
           return (
              <div className='image-mason-container'>
                <div className='image-mason' >
                    <img src={attr.image} />
                </div>
              </div>
            );
        });
        return (
            <Masonry
                className={'my-gallery-class'} // default ''   
            >
                {childElements}
            </Masonry>
          
        );
  }
}

export default Home
